<template>
  <v-list dense expand ref="nav">
    <label for="" v-for="(item, i) in navigatorInfo" :key="i">
      <label for="" v-if="!item.children">
        <v-list-item exact active-class="linkActive" :to="item.path">
          <v-list-item-icon>
            <v-icon v-text="item.icon"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="item.name"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </label>
      <label for="" v-else>
        <v-list-group @click="test(i)" :value="navOne[i]">
          <template v-slot:activator>
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ item.name }}</v-list-item-title>
            </v-list-item-content>
          </template>
          <label for="" v-for="(m, k) in item.children" :key="k">
            <label for="" v-if="!m.children">
              <v-list-item
                exact
                active-class="linkActive"
                :to="m.path"
                style="padding-left: 1.5rem"
              >
                <v-list-item-icon>
                  <v-icon v-text="m.icon"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title v-text="m.name"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </label>
            <label for="" v-else>
              <v-list-group
                :sub-group="true"
                @click="test(i, k)"
                :value="two[i][k]"
              >
                <template v-slot:activator>
                  <v-icon></v-icon>
                  <v-list-item-content>
                    <v-list-item-title style="padding-left: 1rem">{{
                      m.name
                    }}</v-list-item-title>
                  </v-list-item-content>
                </template>
                <label for="" v-for="(b, v) in m.children" :key="v">
                  <v-list-item
                    style="padding-left: 3rem"
                    exact
                    active-class="linkActive"
                    :to="b.path"
                  >
                    <v-list-item-icon>
                      <v-icon v-text="b.icon"></v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title v-text="b.name"></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </label>
              </v-list-group>
            </label>
          </label>
        </v-list-group>
      </label>
    </label>
  </v-list>
</template>

<script>
export default {
  name: "my-nav-list",
  props: {
    navigatorInfo: Array,
    tag: String,
  },
  data() {
    return {
      one: [],
      two: [],
    };
  },
  watch: {
    "$router.history.current.path"(n) {
      console.log(n);
    },
  },
  mounted() {
    // console.log(this.$refs.nav);
    if (this.navOne) {
      this.one = JSON.parse(JSON.stringify(this.navOne));
    }
    if (this.navTwo) {
      this.two = JSON.parse(JSON.stringify(this.navTwo));
    }

    console.log(this.one, "1111111");
  },
  methods: {
    navGopage(item) {
      //路由路径参数
      this.$emit("changeTag", item);
      this.$router.replace({ path: "/Home/" + item });
    },
    test(e, i) {
      if (e && !i) {
        if (!!this.one[e]) {
          this.one[e] = !this.one[e];
        } else {
          this.one[e] = true;
        }
      } else {
        if (!!this.two[e] && !!this.two[e][i]) {
          this.two[e][i] = !this.two[e][i];
        } else {
          let a = [];
          a[i] = true;
          this.two[e] = a;
        }
      }
      let navP = {
        one: this.one,
        two: this.two,
      };
      console.log(navP);
      this.myCookie.set("navP", JSON.stringify(navP));
    },
  },
  destroyed() {
    // let b = {
    //   one: [0, 0, 0],
    //   two: [1, 1, 1],
    // };
    // let obj = JSON.stringify(b);
    // this.myCookie.set("ZHPTCookies", obj);
    // console.log(JSON.parse(this.myCookie.get("ZHPTCookies")), "00000");
  },
};
</script>
<style  scoped>
.navBox::-webkit-scrollbar {
  display: none;
}
.linkActive {
  color: red !important;
  transition: all 0.3s;
}
</style>